.header {
  height: 2.9em;
  font-size: 1.3em;
  position: fixed;
  width: 160%;
  z-index:999;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.004),
              0 6.7px 5.3px rgba(0, 0, 0, 0.008),
              0 12.5px 10px rgba(0, 0, 0, 0.03),
              0 22.3px 17.9px rgba(0, 0, 0, 0.002),
              0 41.8px 33.4px rgba(0, 0, 0, 0.003),
              0 100px 80px rgba(0, 0, 0, 0.01);
}
.profileIcons{
  height:auto;
  width:2.3em;
  padding:2px;
  margin-left:10px;
}
#stack {
  align-items:center;
  text-align:center;
  position:relative;
  top:15em;
}
.stack h3 {
  font-family:'Montserrat'
}
.cardProject h3{
  font-family:'Montserrat'
}
.stackImg{
  width:120px;
}

.stackCard{
  margin: 0 auto;
  width: 90%;
  overflow:hidden;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
              0 6.7px 5.3px rgba(0, 0, 0, 0.048),
              0 12.5px 10px rgba(0, 0, 0, 0.06),
              0 22.3px 17.9px rgba(0, 0, 0, 0.072),
              0 41.8px 33.4px rgba(0, 0, 0, 0.086),
              0 100px 80px rgba(0, 0, 0, 0.12);
}
.stackCard p {
  font-weight:bold;
  color:rgba(100, 96, 90, 0.829);
}
.stackParagraphs{
  text-align: center;
}

.project {
  align-items: center;
}

.cardProject {
  max-height:100%;
  width: 50%;
  margin: 0 auto;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
              0 6.7px 5.3px rgba(0, 0, 0, 0.048),
              0 12.5px 10px rgba(0, 0, 0, 0.06),
              0 22.3px 17.9px rgba(0, 0, 0, 0.072),
              0 41.8px 33.4px rgba(0, 0, 0, 0.086),
              0 100px 80px rgba(0, 0, 0, 0.12);   
}

.cardImg {
  max-height: 300px;
  border: solid black .1px;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.094)
}
.landingPage {
  align-items: center;
  position: relative;
  top: 5.5em;
}
.landing p{
  font-size: 1.2em;
  font-family:'Raleway';
}
.name {
  font-family:'Montserrat';
  font-weight:300;
  font-size: 4em;
  text-align:left;
}
.profilePic {
  height: 500px;
}
footer{
  position:relative;
  top:15em;
  width:100%;
  margin-top:100px;
  -webkit-box-shadow: inset 0 20px 20px -20px rgba(0,0,0,0.3);
  -moz-box-shadow: inset 0 20px 20px -20px rgba(0,0,0,0.3);
  box-shadow: inset 0 20px 20px -20px rgba(0,0,0,0.3);
}
@media screen and (device-width: 375px) and (device-height: 667px) and (-webkit-device-pixel-ratio: 2) {
  .profilePic {
    height: 300px;
  }

}